<template>
  <div class="detailed" :style="item">
     <Goback title="收支明细"></Goback>
     <div class="detailed_switch">
       <div class="switch_box" 
       :class="switchId==1?'box_item':''"
       @click="()=>this.switchId=1"
       >收入</div>
       <div class="switch_line"></div>
       <div class="switch_box" 
       :class="switchId==2?'box_item':''"
       @click="()=>this.switchId=2"
       >支出</div>
     </div>
     <div class="detailed_box">
       <div class="box_children">
         <template v-if="switchId==1">
           <div class="children_item" v-for="(item,index) in 4" :key="index">
             <div class="item_left">
               <div>诉讼收入</div>
               <div class="left_text">2021.08.27</div>
             </div>
             <div class="item_right">+2000
               <img src="@/assets/images/mero.png" alt="" srcset="">
             </div>
           </div>
         </template>
         <template v-else>
           <div class="children_item" v-for="(item,t) in 4" :key="t">
            <img src="@/assets/images/mero.png" alt="">
            <div class="item_box">
              <div class="boxLeft">
                <div>-2000</div>
                <div class="boxLeft_text">2021.08.27</div>
              </div>
              <div class="boxRight">
                转账成功
              </div>
            </div>
           </div>
         </template>
       </div>
     </div>
  </div>
</template>

<script>
export default {
    name:'detailed',
    data(){
      return {
        switchId:2,
        item:{height:'',width:''}
      }
    },
    created(){
      this.item = {
      height: document.documentElement.clientHeight + "px",
      width: document.documentElement.clientWidth + "px"
    };
    }
}
</script>

<style lang="scss">
@import './index.scss';
</style>


